body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: linear-gradient(#292B3F 65%, #323246);
}
body.overlay {
  overflow: hidden;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

ul li {
  color: #FFFFFF;
  font-size: 2rem;
}

.admin-html {
  height: 100%;
}

.container {
  width: 90%;
  margin: 2.5rem auto;
}
