  }
}
@media (min-width: 951px) {
  h4 {
    font-size: 1.8rem;
  }
}
@media (min-width: 1251px) {
  h4 {
    font-size: 2rem;
  }
}
@media (min-width: 1441px) {
  h4 {
    font-size: 2.2rem;
  }
}
@media (min-width: 2001px) {
  h4 {
    font-size: 2.4rem;
  }
}

p {
  font-size: 1.6rem;
}
@media (min-width: 951px) {
  p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1251px) {
  p {
