textarea {
  min-height: 10rem;
}

option, select {
  text-transform: capitalize;
}

.no-display {
  display: none !important;
}

.no-click {
  pointer-events: none;
}

.light-bg {
  background-color: #323246;
  margin-top: 4rem;
  padding: 2rem;
  border-radius: 2rem;
}

.div-bg {
  background-color: #323246;
  margin-top: 4rem;
  padding: 2rem;
}

.text-left {
  text-align: left;
  background-color: transparent;
  margin: 0;
  margin-bottom: 4rem;
}

.slider-main {
  margin-top: 4rem;
  position: relative;
}
.slider-main::after {
  clear: both;
  display: table;
  content: "";
}

.wrapper {
  display: flex;
  gap: 2rem;
  align-items: center;
  overflow-x: hidden;
}
.wrapper .main-container {
  background-color: #323246;
  padding: 2rem;
  border-radius: 2rem;
}

.buttons {
  position: absolute;
  width: 100%;
  top: 45%;
  left: 0;
  z-index: 1;
  display: flex;
}
.buttons .slider-next {
  box-shadow: -2px -2px 5px #202020;
  margin-right: -3.5rem;
  z-index: 1;
  right: 0;
}
.buttons .slider-prev {
  box-shadow: 2px 2px 5px #202020;
  margin-left: -3.5rem;
  z-index: 1;
  left: 0;
}
.buttons .slider-next, .buttons .slider-prev {
  border: none;
  background-color: rgba(255, 255, 255, 0.3);
  color: #292B3F;
  font-size: 3rem;
  font-weight: 700;
  padding: 2.5rem;
  display: grid;
  place-items: center;
  border-radius: 50%;
  position: absolute;
}
.buttons .slider-next:hover, .buttons .slider-prev:hover {
  cursor: pointer;
}

.bloque-btn {
  display: flex;
  flex-direction: column;
}
.bloque-btn a {
  margin-bottom: 2rem;
}

.disabled {
  pointer-events: none;
}

.disabled-btn {
  background-color: #505070 !important;
  color: #171823 !important;
  pointer-events: none;
}
.disabled-btn:hover {
  background-color: #505070 !important;
  color: #171823 !important;
}

.mensaje-max {
  color: #36DE8C;
  font-weight: 700;
}

.text-yellow {
  color: #FD9526;
}

.text-blue {
  color: #29CBFC;
}

.text-green {
  color: #36DE8C;
}

.tel-form {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto;
  gap: 0.5rem;
  grid-auto-flow: column;
}
@media (min-width: 951px) {
  .tel-form {
    grid-template-columns: auto 85%;
  }
}

.tel-input {
  grid-column: 2/3;
}

.tel-index {
  all: unset;
  font-size: 1.6rem;
  padding: 1rem;
  background-color: #292B3F;
  color: #FD9526;
  grid-column: 1/2;
  border-radius: 1rem;
  max-width: 3rem;
  cursor: not-allowed;
}
@media (min-width: 951px) {
  .tel-index {
    max-width: 5rem;
  }
}

.mTop-0 {
  margin-top: 0;
}

.mBottom-0 {
  margin-bottom: 0;
}

.mTop-1 {
  margin-top: 1rem;
}

.mTop-3 {
  margin-top: 1.5rem;
}
@media (min-width: 951px) {
  .mTop-3 {
    margin-top: 3rem;
  }
}

.mTop-5 {
  margin-top: 2.5rem !important;
}
@media (min-width: 951px) {
  .mTop-5 {
    margin-top: 5rem !important;
  }
}

@media (max-width: 950px) {
  .mTop-5--only-mobile {
    margin-top: 5rem;
  }
}

.mTop-10 {
  margin-top: 5rem;
}
@media (min-width: 951px) {
  .mTop-10 {
    margin-top: 10rem;
  }
}

.mBottom-5 {
