}

@media (max-width: 950px) {
  .mBottom-5--only-mobile {
    margin-bottom: 5rem;
  }
}

.mBottom-10 {
  margin-bottom: 5rem;
}
@media (min-width: 951px) {
  .mBottom-10 {
    margin-bottom: 10rem;
  }
}

@media (max-width: 950px) {
  .mBottom-10--only-mobile {
    margin-bottom: 10rem;
  }
}

.center {
  text-align: center;
}

.text-yellow {
