.option {
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background-color: #292B3F;
  color: #FFFFFF;
  border: 0.1rem solid #FD9526;
}
.option:hover {
  background-color: #FD9526;
  color: #292B3F;
  font-weight: 700;
}

.overlay {
  overflow: hidden !important;
  overflow-y: hidden !important;
}

@media (min-width: 951px) {
  .flex {
    display: flex;
    gap: 2rem;
  }
}

@media (min-width: 951px) {
  .flex--gap-3 {
    display: flex;
    gap: 3rem;
  }
}

@media (min-width: 951px) {
  .flex--gap-5 {
    display: flex;
    gap: 5rem;
  }
}

.flex-icon {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
@media (min-width: 951px) {
  .grid-2 {
    grid-template-columns: 1fr 1fr;
  }
}

.block {
  display: block;
  width: 100%;
}

.deleteModal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  overflow: auto;
}
.deleteModal__modal {
  margin: 5rem 0;
  background-color: #292B3F;
  padding: 3rem;
  width: 90%;
  border-radius: 1rem;
  border-width: 0.2rem;
  border-color: #FD9526;
  border-style: solid;
  position: relative;
  z-index: 11;
}
@media (min-width: 951px) {
  .deleteModal__modal {
    width: 40%;
  }
}
.deleteModal__icon {
  color: #FD9526;
  text-align: center;
  font-size: 2rem;
}
@media (min-width: 951px) {
  .deleteModal__icon {
    font-size: 3rem;
  }
}
.deleteModal__paragraph {
  margin-top: 0.5rem;
}
.deleteModal__btn-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: #FD9526;
  font-size: 2.6rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 951px) {
  .deleteModal__btn-close {
    font-size: 4rem;
  }
}

@media (min-width: 951px) {
  .only--mobile {
    display: none;
  }
}

.only--desktop {
  display: none !important;
}
@media (min-width: 951px) {
  .only--desktop {
    display: block !important;
  }
}

.alert-style {
  border: 0.2rem solid #ff3939;
  padding: 0 0.5rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}

.note-style {
  border: 0.2rem solid #29CBFC;
  background-color: #1f2030;
  padding: 1rem;
  border-radius: 0.5rem;
  line-height: 1.2;
}

.btn-search {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-50%, -50%);
