}
@media (min-width: 951px) {
  .btn-search {
    width: 2.5rem;
    right: 0;
  }
}
@media (min-width: 1251px) {
  .btn-search {
    width: 3rem;
  }
}

.btn-menu {
  filter: sepia(100%) grayscale(1) brightness(1);
}
.btn-menu.active {
  filter: none;
}
.btn-menu:hover {
  filter: none;
}

.btn-dropdown {
  cursor: pointer;
}

.btn-lang {
