  padding: 0.5rem 2rem;
  color: #FFFFFF;
  border-radius: 0.5rem;
  font-size: 1.4rem;
}
.btn-lang:hover {
  cursor: pointer;
  background-color: #FD9526;
}

.btn-submit, .btn-submit--post, .btn-submit--extra, .btn-submit--form {
  background-color: #FD9526;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  font-size: 1.4rem;
  width: 100%;
  border-radius: 1rem;
  color: #292B3F;
}
